@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
    font-family: 'Poppins', sans-serif;
    max-width: 100%;
}

.head {
    text-align: center;
    font-size: 4rem;
}

.nav-item {
    padding: 10px 20px;
    text-align: center;
    font-size: 1.3rem;
    border-radius: 15px;
}

.nav-item:hover {
    transition: 0.3s;
}

nav a {
    text-decoration: none;
}

nav {
    border-bottom: 0.4px solid;
    margin-bottom: 25px;
}

.navbar-toggler i {
    font-size: 20px;
}

.mode {
    text-align: center;
    padding-top: 8px;
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
}

.mode i {
    font-size: 30px;
}

.card-img-top {
    width: 80%;
}

/* .login-fields {
    width: 50%;
    margin: auto;
} */

.login-fields input {
    width: 100%;
    border: none;
    border-bottom:2px solid #0D6EFD ;
    background: none;
    text-align: center;
    font-size: 30px;
}

.login-fields button {
    margin-top: 20px;
    width: 100%;
}

.navbar-brand {
    font-size: 35px;
    text-align: center;
}

.not img {
    display: block;
    height: auto;
    width: 50%;
    margin: auto;
}

.loader {
    text-align: center;
    margin: 100px;
}

